import {mapState, mapGetters} from 'vuex';
import basic from './basic';
import module from './module';

export default {
  components: {
    detailBasic: basic,
    detailModule: module,
  },
  computed: {},
  watch: {},
  data() {
    return {};
  },
  methods: {},
  async onLoad(e) {
    uni.showLoading({
      title: '加载中...',
      mask: false,
    });
    await this.$store.dispatch('detail/setApp', e.app);
    uni.hideLoading();
  },
};
